@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,900;1,400;1,900&family=Raleway:ital,wght@0,400;0,900;1,400;1,900&display=swap");
:root {
  --c-eggplant: hsla(300, 35%, 21%, 1);
  --c-eggplant-dark: #221022;
  --c-watermelon: #ee5050;
  --c-watermelon-dark: #b52f2f;
  --c-yolk: #f3cf50;
  --c-yolk-dark: #cfaa4a;
  --c-navy: #161135;
  --c-purple-berry: #852a85;
  --c-ghost-berry: #cbc0b7;
  --c-mint: #50f3b8;
  --c-chip: #482e23;
  --c-dark-chocolate: #241616;
  --c-peach: #e5a97e;
  --c-leaf-green: #234836;
  --c-deep-green: #122118;
  --bg-color: var(--c-eggplant-dark);
  --fg-color: var(--c-yolk);
  --accent-color: var(--c-eggplant);
}

html {
  scroll-behavior: smooth;
}

.watermelon {
  --bg-color: var(--c-watermelon);
  --fg-color: var(--c-eggplant-dark);
  --accent-color: var(--c-watermelon-dark);
}

.eggplant {
  --bg-color: var(--c-eggplant-dark);
  --fg-color: var(--c-yolk);
  --accent-color: var(--c-eggplant);
}

.berry {
  --bg-color: var(--c-navy);
  --fg-color: var(--c-ghost-berry);
  --accent-color: var(--c-purple-berry);
}

.mintchip {
  --bg-color: var(--c-dark-chocolate);
  --fg-color: var(--c-mint);
  --accent-color: var(--c-chip);
}

.peachtree {
  --bg-color: #E6D9D0;
  --fg-color: #17171e;
  --accent-color: var(--c-peach);
}

.yolk {
  --bg-color: var(--c-yolk);
  --fg-color: var(--c-eggplant);
  --accent-color: var(--c-yolk-dark);
}

.bloggy {
  --bg-color: var(--c-deep-green);
  --fg-color: var(--c-eggplant);
  --accent-color: var(--c-yolk-dark);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  --font-size: 1rem;
  line-height: 1.25;
  margin: 0;
  font-family: "Raleway", sans-serif;
  background-color: var(--bg-color);
  color: var(--fg-color);
  transition: background-color 0.5s, color 0.5s;
}
body[data-scroll-locked] {
  overflow: hidden;
}

main {
  position: relative;
}
@media (prefers-reduced-motion: no-preference) {
  main {
    animation: scalePage 1s ease forwards;
    transform-origin: top;
  }
}

@keyframes scalePage {
  0% {
    left: -10%;
    opacity: 0;
  }
  100% {
    left: 0;
    opacity: 1;
  }
}
.page-section {
  padding: 0 50px 50px 50px;
}

.about-grid {
  display: grid;
  gap: 1rem;
  min-height: min(100vw, 80vh);
  width: 100%;
  padding: 10% 15%;
}
.about-grid p {
  --font-size-from: 14;
  --font-size-to: 28;
  --viewport-unit-converter2: 2vw;
}

a {
  color: var(--fg-color);
  font-weight: bold;
  letter-spacing: 0.2ch;
}

.section-grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  min-height: min(100vw, 80vh);
  width: 100%;
}
.section-grid img {
  width: min(256px, 100%);
  justify-self: center;
}
.section-grid .l {
  text-align: left;
}
.section-grid .c {
  text-align: center;
}
.section-grid .r {
  text-align: right;
}
.section-grid .vc {
  align-self: center;
}
.section-grid .vt {
  align-self: start;
}
.section-grid .vb {
  align-self: end;
}

p {
  transition: color 0.5s;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Playfair Display", serif;
  font-style: italic;
  margin-block: 1rem;
  transition: color 0.5s;
}

h1 {
  font-weight: black;
  --font-size-from: 28;
  --font-size-to: 54;
}

h2 {
  --font-size-from: 44;
  --font-size-to: 100;
  margin-block: 2rem;
  font-style: normal;
  font-weight: 900;
}

h3 {
  --font-size-from: 18;
  --font-size-to: 32;
  margin-block: 1rem;
}

.hero {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.hero .hero-content {
  grid-column-start: 2;
  align-self: center;
  display: grid;
}
.hero .hero-content h2 {
  max-width: max(50%, 1000px);
  grid-row-start: 2;
  grid-column-start: 1;
}
.hero .hero-content .underlay {
  color: var(--accent-color);
  grid-row-start: 2;
  grid-column-start: 1;
  z-index: -1;
  position: relative;
  top: -0.88ch;
}
.hero nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  gap: 1rem;
}
.hero nav ul li a {
  font-family: "Playfair Display", serif;
  font-style: italic;
  --font-size-from: 18;
  --font-size-to: 32;
  color: var(--fg-color);
  text-decoration: none;
  transition: color 0.5s;
}
.hero nav ul li a::after {
  display: block;
  content: "";
  width: 100%;
  height: 4px;
  background-color: var(--fg-color);
  position: relative;
  opacity: 0;
  top: 5px;
  transition: opacity 0.4s, top 0.4s;
}
.hero nav ul li a:hover::after, .hero nav ul li a:active::after, .hero nav ul li a:focus::after {
  opacity: 1;
  top: 20px;
}

.hero.mini {
  height: max(600px, 60vh);
  max-width: 1480px;
  margin: auto;
  padding-top: max(10vh, 50px);
}
@media screen and (max-width: 600px) {
  .hero.mini {
    height: max(400px, 50vh);
  }
}
.hero.mini .hero-content {
  grid-column-start: 1;
  max-width: 290px;
}
.hero.mini .hero-content > a {
  text-decoration: none;
}
.hero.mini .hero-content > a:hover, .hero.mini .hero-content > a:active, .hero.mini .hero-content > a:focus {
  text-decoration: underline;
}
.hero.mini h2 {
  --font-size-from: 28;
  --font-size-to: 32;
}
.hero.mini .bg {
  max-height: 50vh;
  position: static;
}

.bg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  max-height: 100vh;
  max-width: 100vw;
  z-index: -1;
  opacity: 0.5;
  display: grid;
  place-items: center;
}
.bg svg {
  fill: var(--accent-color);
  width: 100%;
  max-height: 95%;
  transition: fill 0.5s;
}
.bg svg path {
  transition: fill 0.5s;
}

.accented {
  position: relative;
  width: fit-content;
  line-height: 0.65;
}
.accented::before {
  content: "";
  display: block;
  position: Absolute;
  z-index: -1;
  top: 0.5ch;
  left: 0.5ch;
  height: 10vw;
  aspect-ratio: 1/1;
  background-color: var(--accent-color);
  transition: background-color 0.5s;
}

* {
  --viewport-unit-converter: 1vw;
  --viewport-unit-converter2: 1.25vw;
  --fz-from: var(--font-size-from, var(--min-font-size));
  --fz-to: var(--font-size-to, var(--max-font-size));
  --fz-slope: (var(--fz-to) - var(--fz-from)) / (var(--viewport-to) - var(--viewport-from)) * 100;
  --fz-intercept: (var(--viewport-to) * var(--fz-from) - var(--viewport-from) * var(--fz-to)) / (var(--viewport-to) - var(--viewport-from));
  --font-size: calc(var(--fz-slope) * var(--viewport-unit-converter) + var(--fz-intercept) * 1px);
  --line-height: calc(var(--fz-slope) * var(--viewport-unit-converter2) + var(--fz-intercept) * 1px);
  --min-fz-px: calc(var(--min-font-size) * 1px);
  --max-fz-px: calc(var(--max-font-size) * 1px);
  --clamp: clamp(var(--min-fz-px), var(--font-size), var(--max-fz-px));
  --max: var(--has-max, var(--min));
  --min: var(--has-min, var(--font-size));
  --has-max: min(var(--max-fz-px), var(--font-size));
  --has-min: max(var(--min-fz-px), var(--font-size));
  font-size: var(--clamp, var(--max));
  line-height: var(--line-height);
  --viewport-from: 420;
  --viewport-to: 1920;
}

.h-track {
  width: 100%;
  height: 100vh;
  overflow-x: auto;
  position: relative;
  overflow-y: hidden;
}
.h-track .h-content-container {
  position: relative;
  max-width: unset;
  width: fit-content;
  height: 100%;
  display: flex;
  align-items: stretch;
  transition: transform 0.2s linear;
}
.h-track .h-content-container > * {
  min-width: 100vw;
  height: 100%;
}
.h-track .h-content-container > *.half {
  min-width: 50vw;
}

.invisible-scrollbar {
  scrollbar-width: none;
}

.invisible-scrollbar::-webkit-scrollbar {
  display: none;
}

.progress-bar {
  margin: 10px auto;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  width: 100%;
  max-width: 50%;
  height: 10px;
  border-radius: 5px;
  border: 1px solid var(--text-color);
  overflow: hidden;
}
.progress-bar::before {
  content: "";
  display: block;
  background-color: var(--text-color);
  height: 100%;
  width: var(--scroll-pct);
}

#social-links {
  margin-top: 5%;
}
#social-links ul {
  gap: 2rem;
}
#social-links li {
  max-width: clamp(25px, 25px + 1vw, 40px);
  align-self: end;
}
#social-links li a {
  align-self: end;
}
#social-links svg {
  width: 100%;
  height: auto;
}

a.link {
  text-decoration: none;
}
a.link:hover, a.link:active, a.link:focus {
  text-decoration: underline;
}

footer {
  margin: 50px;
  min-height: 300px;
  display: grid;
  justify-content: center;
  align-items: end;
}
footer .content {
  display: grid;
}
footer svg {
  max-width: 80px;
  text-align: center;
  justify-self: center;
}